<template>
    <div>
      <h1>Project Data</h1>
      <table>
        <thead>
          <tr>
            <th>Project Name</th>
            <th>Description</th>
            <th>Category</th>
            <th>Developer</th>
            <th>Completion Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="projects.length === 0">
            <td colspan="6" class="text-center">No data available</td>
          </tr>
          <tr v-for="project in projects" :key="project.id">
            <td>{{ project.name }}</td>
            <td v-html="project.description"></td>
            <td>{{ project.category }}</td>
            <td>{{ project.developer }}</td>
            <td>{{ project.completionDate || 'N/A' }}</td>
            <td>
              <button @click="editProject(project)">Edit</button>
              <button @click="deleteProject(project)">Delete</button>
            </td>
          </tr>
          <template v-for="project in projects" :key="project.id">
            <tr v-if="project.unitTypes.length > 0">
              <td colspan="6">
                <table>
                  <thead>
                    <tr>
                      <th>Unit Type</th>
                      <th>Description</th>
                      <th>Price</th>
                      <th>Rooms</th>
                      <th>Bathrooms</th>
                      <th>Car Parks</th>
                      <th>Build Up Area</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="unit in project.unitTypes" :key="unit.name">
                      <td>{{ unit.name }}</td>
                      <td v-html="unit.description"></td>
                      <td>{{ unit.price }}</td>
                      <td>{{ unit.room }}</td>
                      <td>{{ unit.bathRoom }}</td>
                      <td>{{ unit.carPark }}</td>
                      <td>{{ unit.buildUpArea }} sq ft</td>
                    </tr>
                  </tbody>
                </table>
              </td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  import { loadProjects } from '../utils/contactUtils.js';
  
  export default {
    data() {
      return {
        projects: [],
      };
    },
    async mounted() {
      await this.loadProjectsData('residensi bukit jalil');
    },
    methods: {
      async loadProjectsData(projectFilter) {
        try {
          const projects = await loadProjects(projectFilter);
          console.log('Loaded projects:', projects);
          this.projects = projects;
        } catch (error) {
          console.error('Error loading projects:', error);
        }
      },
      editProject(row) {
        console.log('Editing project:', row);
        // Implement your edit logic here
      },
      deleteProject(row) {
        console.log('Deleting project:', row);
        // Implement your delete logic here
      },
    },
  };
  </script>
  
  <style scoped>
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  th {
    background-color: #f2f2f2;
  }
  .text-center {
    text-align: center;
  }
  </style>
  