import { gql } from '@apollo/client/core';

export const fetchContactsRatingQuery = gql`
query {
  leads(filters: { rating: { gt: 0 } }, pagination: { limit: 1000 }) {
    data {
      id
      attributes {
        no
        project
        user
        name
        language
        phoneNumber
        status
        phoneId
        interaction
        lastCall
        nextCall
        called
        rating
        dialedDate
        sourceDate
        contacted
      
        message
        doc
        stage
        source
        occupation
        clientType
        process
        task
        delegate
        address
        advUrl
        advUrlDate
        facebookUrl
        notionUrl
        updatedAt
      }
    }
  }
}
`;

export const  fetchContactsQuery = gql`
query($project: String!) {
  leads(filters: { project: { eq: $project } }, pagination: { limit: 1000 }) {
    data {
      id
      attributes {
        no
        project
        user
        name
        phoneNumber
        status
        phoneId
        interaction
        lastCall
        nextCall
        called
        rating
        dialedDate
        sourceDate
        contacted
        language
        message
        doc
        stage
        source
        occupation
        clientType
        process
        stage
        task
        delegate
        address
        advUrl
        advUrlDate
        facebookUrl
        notionUrl
        updatedAt
       
      }
    }
  }
}
`;

export const  fetchContactsFilterQuery = gql`
query ($project: String!) {
  leads(
    filters: { 
      project: { eq: $project },
      status: { notContains: "ni" },
      called: { lte: 5 }
    },
    pagination: { limit: 1000 }
  ) {
    data {
      id
      attributes {
        no
        project
        user
        name
        phoneNumber
        status
        phoneId
        interaction
        lastCall
        nextCall
        called
        rating
        dialedDate
        sourceDate
        contacted
        language
        message
        doc
        stage
        source
        occupation
        clientType
        process
        stage
        task
        delegate
        address
        advUrl
        advUrlDate
        facebookUrl
        notionUrl
        updatedAt
      }
    }
  }
}`;


