<template>
    <div class="custom-form-container">
        <v-form v-if="contact" ref="contactForm" style="max-width: 1800px; margin: 0 auto;">


            <h2>Edit Contact: {{ contact.id }} : {{ index }}-{{ contact.no }}/{{ contact.project }}/{{ contact.name
                }}/{{
                    contact.phoneNumber }}</h2>

            <!-- Existing fields zzz -->

            <!-- <v-textarea v-model="editContactData.interaction" label="Interaction" outlined
                    @keydown="handleKeydown" /> -->

            <!-- <v-menu v-model="isMenuActive" activator="parent" transition="scale-transition">
                    <v-list>
                        <v-list-item v-for="(option, index) in filteredOptions" :key="index"
                            @click="selectOption(option)">
                            <v-list-item-title>{{ option }}</v-list-item-title>
                        </v-list-item>
                    </v-list>
                </v-menu> -->
            <v-menu v-model="isMenuActive" transition="scale-transition" max-height="300px">
                <template #activator="{ on, attrs }">
                    <v-textarea v-model="editContactData.interaction" label="Interaction" outlined v-bind="attrs"
                        v-on="on" @keydown="handleKeydown" />
                </template>
                <v-list>
                    <v-list-item v-for="(option, index) in filteredOptions" :key="index" @click="selectOption(option)">
                        <v-list-item-title>{{ option }}</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>

            <!-- <v-menu v-model="isMenuActive" activator="parent" transition="scale-transition">
                    <v-list>
                        <v-list-item v-for="(option, index) in filteredOptions" :key="index"
                            @click="selectOption(option)">
                            <v-list-item-title>{{ option }}</v-list-item-title>
                        </v-list-item>
                    </v-list>
                </v-menu> -->
            <v-text-field label="Doc" v-model="editContactData.doc" class="edit-mode"></v-text-field>
            <v-text-field label="Status" v-model="editContactData.status" class="edit-mode"></v-text-field>
            <v-text-field label="Rating" v-model.number="editContactData.rating" type="number"
                class="edit-mode"></v-text-field>
            <v-text-field label="Called (0 or 1)" v-model.number="editContactData.called" type="number"
                class="edit-mode"></v-text-field>
            <v-text-field label="Contacted (0 or 1)" v-model.number="editContactData.contacted" type="number"
                class="edit-mode"></v-text-field>

            <!-- Dropdown for Client Type -->
            <v-select v-model="editContactData.clientType" :items="clientTypes" label="Client Type" outlined></v-select>

            <!-- Address and advUrl fields -->
            <v-text-field label="Address" v-model="editContactData.address" class="edit-mode"></v-text-field>
            <v-text-field label="advUrl" v-model="editContactData.advUrl" class="edit-mode"></v-text-field>
            <v-text-field label="advUrl Date" v-model="editContactData.advUrlDate" type="date"
                class="edit-mode"></v-text-field>

            <!-- New Fields for Facebook and NotionURLs -->
            <v-text-field label="facebookUrl" v-model="editContactData.facebookUrl" class="edit-mode"></v-text-field>
            <v-text-field label="NotionURL" v-model="editContactData.notionUrl" class="edit-mode"></v-text-field>

            <!-- Process/Stage/Task Selector -->

            <!-- <ProcessStageTaskSelector :selectedProcess="selectedProcess" :selectedStage="selectedStage"
                        :selectedTask="selectedTask" @update:selectedProcess="updateProcess"
                        @update:selectedStage="updateStage" @update:selectedTask="updateTask" /> -->

            <!-- <ProcessStageTaskSelector :initial-value="initialValue" :selectedProcess="selectedProcess" />-->


            <v-select v-model="editContactData.phoneId" :items="phoneIdOptions" label="PhoneId" outlined></v-select>


            <div>

                <ProcessStageTaskSelector :initial-value="initialValue"
                    @update:selectedProcess="handleSelectedProcessChange"
                    @update:selectedStage="handleSelectedStageChange" @update:selectedTask="handleSelectedTaskChange" />
            </div>




            <!-- Dropdown for PhoneId -->
            <h2>Process: {{ editContactData.process }}</h2>
            <h2>stage: {{ editContactData.stage }}</h2>

            <h2>Last Call Date: {{ editContactData.lastCall }}</h2>
            <h2>Next Call Date: {{ editContactData.nextCall }}</h2>
            <h2>Status: {{ editContactData.status }}</h2>

            <h2>task: </h2>

            <EnhancedEditor :key="editContactData.task" ref="editorRef" :initialData="editContactData.task" />

            <v-btn @click="saveContact" color="primary" class="mx-1" rounded
                style="width: 20%; min-width: 20px;height: 70px;">Save</v-btn>
            <v-btn rounded @click="showStatusDialog = true" :disabled="currentIndex === contacts.length - 1"
                color="#00AA55" class="mx-1" style="width: 20%; min-width: 20px;height: 70px;">
                Save <v-icon>mdi-arrow-right</v-icon>
            </v-btn>

            <v-btn rounded @click="cancelEdit" color="blue"
                style="width: 20%; min-width: 20px;height: 70px;">Cancel</v-btn>

            <v-btn rounded @click="deleteContact" color="red" class="mx-1"
                style="width: 20%; min-width: 20px;height: 70px;">
                <v-icon>mdi-delete</v-icon> Delete
            </v-btn>


            <!-- <v-btn rounded @click="makeCall2(editContactData, selectedUser, index, contacts)"
                    :color="isPhoneDisabled(contact) ? 'grey' : 'primary'"
                    style="width: 20%; min-width: 50px; height: 70px;"
                    :disabled="isPhoneDisabled2(contact) == 1 || isPhoneDisabled2(contact) == 0" class="mx-1">
                    <v-icon
                        v-if="isPhoneDisabled2(contact) == 1 || isPhoneDisabled2(contact) == 0">mdi-phone-off</v-icon>
                    <v-icon v-else>mdi-monitor-cellphone-star</v-icon>
                </v-btn> -->

            <v-btn rounded @click="makeCall2(editContactData, selectedUser, index, contacts)" :color='primary'
                style="width: 20%; min-width: 50px; height: 70px;" class="mx-1">

                <v-icon>mdi-monitor-cellphone-star</v-icon>
            </v-btn>
            <v-btn :disabled="isPhoneDisabled(editContactData)" rounded :href="`tel:${editContactData.phoneNumber}`"
                color="primary" @click="handleCallClick" style="width: 20%; min-width: 50px; height: 70px;"
                class="mx-1">

                <v-icon>mdi-phone-in-talk</v-icon>
            </v-btn>



            <div>
                <!-- <v-btn @click="openStatusDialog">Select Status</v-btn> -->


                <statusDialog v-model:showDialog="showStatusDialog" :selectedStatus="status"
                    @updateStatus="handleStatusUpdate" />

                <p>Selected Status: {{ status }}</p>
            </div>



            <!-- Displaying the call log -->
            <div>
                <h3>Call Log</h3>
                <ul>
                    <li v-for="(log, index) in callLog" :key="index">
                        {{ log.name }} - {{ log.phoneNumber }} - {{ log.time }}
                    </li>
                </ul>
            </div>


        </v-form>
    </div>
</template>


<script>
import { useMutation } from '@vue/apollo-composable';
import { ref, watch, onMounted } from 'vue';
import { UPDATE_CALLME, convertDMY2YMDtime, getDeviceInfo, isPhoneDisabled2, isPhoneDisabled } from '../utils/contactUtils';
import ProcessStageTaskSelector from './ProcessStageTaskSelector';

import statusDialog from './statusDialog';

import { optionMsgs } from '../utils/message.js';
import EnhancedEditor from './EnhancedEditor.vue'; 

export default {
    name: "editFromy",
    props: {
        contact: Object,
        onSave: Function,
        onDelete: Function,
        onCancel: Function,
        index: Number,
        selectedUser: String,
        selectedPhoneId: String,
        contacts: Object,
        nextContact: Function
    },
    components: {
        ProcessStageTaskSelector,
        statusDialog,
        EnhancedEditor
    },
    setup(props) {
        const editorRef = ref(null); // Define the reference
        const showStatusDialog = ref(false);


        const process = ref('');
        const status = ref('');
        const stage = ref('');
        const task = ref('');
        const delegate = ref('');

        const isMenuActive = ref(false);
        const filteredOptions = ref([]);
        //         const optionMsgs = [`
        // *Rental
        // 1. Name: 
        // 2. Nationality: 
        // 3. Gender: 
        // 4. No of people staying: (Pls stated relationship if more than 1 ppl)
        // 5. Furnishing:
        // 6. Pets: 
        // 7. Move in date: 
        // 8. Working / Studying:
        // 9. Company / Institution:
        // 10. Budget: 

        // Kindly share me some basic information for me to prior viewing arrangement & propose the suitable unit.

        //         `, `
        // *Sales
        // 1. Built up area
        // 2. No of room
        // 3. No of  bathroom
        // 4. No of parking lot / no car park
        // 5.Tenanted/ own occupied/ vacant
        // * If tenanted
        // Current rental& tenancy expiry date
        // 6. Basic/ partly/ fully furnished
        // 8.Strata title obtained?
        // 9. Maintenance fee & sinking fund?
        // 10. One vendor name or 2 vendors or more?
        // 11. Free from emcumbrances or still have housing loan with bank 
        // 12. Expected selling price

        // Basically i need the above info yq
        // `, "Call", "Email"];
        //   const selectedProcess = ref(initialValueRef.value);

        const openStatusDialog = () => {
            showStatusDialog.value = true;
        };

        const handleStatusUpdate = (selectedStatus) => {
            confirmStatus(props.index, selectedStatus);
            status.value = selectedStatus;
        };

        const callLog = ref([]);
        const editContactData = ref({
            interaction: '',
            doc: '',
            status: '',
            rating: 0,
            called: 0,
            contacted: 0,
            clientType: '',  // Add clientType to editContactData
            address: '',     // New field for Address
            advUrl: '',         // New field for advUrl
            advUrlDate: '',     // New field for advUrl Date
            process: '',       // New field for Stage
            stage: '',       // New field for Stage
            task: '',       // New field for Stage
            delegate: '',       // New field for Stage
            phoneId: '',      // New field for PhoneId
            facebookUrl: '',
            notionUrl: '',
            // selectedProcess,
            // selectedStage,
            // selectedTask,
        });

        // Define options for the dropdowns
        const clientTypes = ["buyer", "seller", "co broke", "Tenant", "Land Owner"];
        //   const stageOptions = ["Lead", "Opportunity", "Negotiation", "Closed", "Follow-up"];
        const phoneIdOptions = ['Alyssa H', 'Ashton H', 'Alvin S', ''];

        onMounted(() => {
            const storedCallLog = localStorage.getItem('callLog');
            if (storedCallLog) {
                callLog.value = JSON.parse(storedCallLog);
            }

            if (editorRef.value) {
                console.log("EnhancedEditor is available:", editorRef.value);
                // Now you can safely call methods on editorRef.value, e.g.,
                // editorRef.value.getSavedData();
            } else {
                console.error("EnhancedEditor is not available.");
            }
        });


        // function isPhoneDisabled(contact) {
        //     // const status = String(contact.status || '');
        //     //  const interaction = String(contact.interaction || '');
        //     //  const stage = String(contact.stage || '');



        //     // Perform case-insensitive check and substring check
        //     return status.toLowerCase().includes('ni') || interaction.includes('xxx') || stage.trim().toLowerCase() == "x";
        // }

        const deleteContact = () => {
            // Implement the logic to delete the contact, possibly using an API call
            // Remove the contact from the list locally for demonstration
            //  props.contacts.splice(props.index, 1);
            props.onDelete();
            //   console.log('Contact deleted:', props.contact);
        };

        const handleKeydown = (event) => {
            // Detect the '/' key
            if (event.key === '/') {
                isMenuActive.value = true;
                filteredOptions.value = optionMsgs;  // Show all options
            }
        };

        const selectOption = (option) => {
            // Replace the last '/' with the selected option
            editContactData.value.interaction = editContactData.value.interaction.replace(
                '/',
                option
            );
            isMenuActive.value = false;  // Close the dropdown
        };

        function handleSelectedProcessChange(value) {
            editContactData.value.process = value;
            editContactData.value.stage = "";
            editContactData.value.task = "";
            console.log('Selected process:', value);
        }
        function handleSelectedStageChange(value) {
            editContactData.value.stage = value;
            editContactData.value.task = "";
            console.log('Selected stage:', value);
        }
        function handleSelectedTaskChange(value) {
            editContactData.value.task = value;
            console.log('Selected task:', value);
        }
        watch(callLog, (newValue) => {
            localStorage.setItem('callLog', JSON.stringify(newValue));
        });
        // Watch for changes in the contact prop to update the form
        watch(
            () => props.contact,
            (newContact) => {
                if (newContact) {
                    editContactData.value = {
                        id: newContact.id,
                        name: newContact.name || '',
                        phoneNumber: newContact.phoneNumber || '',
                        lastCall: convertDMY2YMDtime(newContact.lastCall, true),
                        nextCall: convertDMY2YMDtime(newContact.nextCall, true),
                        no: newContact.no,
                        interaction: newContact.interaction || '',
                        doc: newContact.doc || '',
                        status: newContact.status || '',
                        rating: newContact.rating || 0,
                        called: newContact.called || 0,
                        contacted: newContact.contacted || 0,
                        clientType: newContact.clientType || '',
                        address: newContact.address || '',
                        advUrl: newContact.advUrl || '',
                        advUrlDate: newContact.advUrlDate || '',
                        process: newContact.process || '',
                        stage: newContact.stage || '',
                        task: newContact.task || '',
                        delegate: newContact.delegate || '',
                        phoneId: newContact.phoneId || '',
                        facebookUrl: newContact.facebookUrl || '',
                        notionUrl: newContact.notionUrl || '',
                    };

                }
            },
            { immediate: true }
        );

        const updateCallCount = () => {
            if (!isPhoneDisabled(editContactData.value)) {
                editContactData.value.called = (function (calledString) {
                    const numberValue = parseInt(calledString, 10);
                    if (!isNaN(numberValue)) {
                        return numberValue + 1;
                    } else {
                        return 1;
                    }
                })(editContactData.value.called);

                editContactData.value.dialedDate = new Date().toLocaleDateString();
            }
        };

        // function updateValue(newValue) {
        //    // this.initialValue = newValue;
        // }

        const handleCallClick = () => {
            updateCallCount();

            const newLogEntry = {
                name: editContactData.value.name,
                phoneNumber: editContactData.value.phoneNumber,
                time: new Date().toLocaleString()
            };


            callLog.value.push(newLogEntry);

            if (callLog.value.length > 24) {
                callLog.value.shift();
            }

            localStorage.setItem('callLog', JSON.stringify(callLog.value));
            showStatusDialog.value = true;
        };

        const confirmStatus = (index, selectedStatus) => {

            // Check if the current contact exists and a status is selected
            if (!props.contacts[index] || !selectedStatus) {
                alert("error 104");
                console.error('Contact or selected status is undefined.');
                return;
            }
            // Create a local copy of the contacts array
            const localContacts = editContactData.value;
            // Append the selected status to the current status of the contact
            localContacts.status = `${selectedStatus} ${localContacts.status}`;
            // Update the contacts array
            //    this.$emit('update:contacts', localContacts);

            // Optionally, you can choose to remove advUrlDate if it's undefined/null
            // delete localContacts[index].updatedAt;
            // delete localContacts[index].edit;
            // delete localContacts[index].editField;
            // delete localContacts[index].id;
            // if (localContacts[index].souceDate === "") delete localContacts[index].id;
            // if (localContacts[index].dialedDate === "") delete localContacts[index].dialedDate;
            // if (localContacts[index].advUrlDate === "") delete localContacts[index].advUrlDate;
            // if (localContacts[index].lastCall === "") delete localContacts[index].lastCall;
            // if (localContacts[index].nextCall === "") delete localContacts[index].nextCall;

            // localContacts[index].sourceDate = convertDMY2YMD(localContacts[index].sourceDate);
            // localContacts[index].dialedDate = convertDMY2YMDtime(localContacts[index].dialedDate);
            // localContacts[index].advUrlDate = convertDMY2YMD(localContacts[index].advUrlDate);
            // localContacts[index].lastCall = convertDMY2YMDtime(localContacts[index].lastCall);
            // localContacts[index].nextCall = convertDMY2YMDtime(localContacts[index].nextCall);

            // Call the CallSuccess function
            if (selectedStatus == "✓,") {
                this.CallSuccess(localContacts);
            }
            props.onSave(localContacts);
            // Move to the next contact
            props.nextContact();
            // Close the dialog
            showStatusDialog.value = false;
        };



        const makeCall2 = async (contact, selectedUser, index, contacts) => {
            await makeCall(contact, selectedUser, index, contacts);
            // handleCallClick();
        };

        const saveTaskToContact = async () => {
            try {

                const savedData = await editorRef.value.getSavedData();
                console.log('Data to save to server:', savedData);
                editContactData.value.task = savedData;
               } catch (error) {
                console.error('Failed to get saved data:', error);
            }
        };


        const saveContact = async () => {
            // if (this.$refs.contactForm) {
            await saveTaskToContact();
            //  }
            props.onSave(editContactData.value);
        };

        const cancelEdit = () => {
            props.onCancel();
        };

        const updateValue = (newValue) => {
            editContactData.value.process = newValue.process;
            editContactData.value.stage = newValue.stage;
            editContactData.value.task = newValue.task;
        };

        const { mutate: updateCallme } = useMutation(UPDATE_CALLME);

        async function makeCall(contact, selectedUser, currentIndex, contacts) {
            try {
                let { name, doc, phoneNumber, status, interaction, lastCall, nextCall, no } = contact;
                lastCall = convertDMY2YMDtime(lastCall, true);
                nextCall = convertDMY2YMDtime(nextCall, true);
                if (selectedUser == null) {
                    alert("please select user");
                    return;
                }
                let user = selectedUser;
                let id = 0;
                if (user.toLowerCase() === "alyssa") id = 3;
                if (user.toLowerCase() === "alvin") id = 4;
                if (user.toLowerCase() === "ashton") id = 2;

                const variables = {
                    id: id, doc: doc, user: user, name: name, phoneNumber: phoneNumber, status: status,
                    interaction: interaction, lastCall: lastCall, nextCall: nextCall, no: no
                };

                await updateCallme(variables);
            } catch (error) {
                alert("error 200");
                console.error('Error updating callme:', error.cause);
            }

            currentIndex = contacts.findIndex(c => c.no === contact.no);
        }

        return {
            //     initialValue,
            isMenuActive,
            filteredOptions,

            handleKeydown,
            selectOption,

            updateCallCount,
            handleCallClick,
            makeCall2,
            editContactData,
            saveContact,
            cancelEdit,
            isPhoneDisabled2,
            isPhoneDisabled,
            clientTypes,
            process,
            stage,
            task,
            delegate,
            phoneIdOptions,
            callLog,

            status,
            openStatusDialog,
            handleStatusUpdate,
            showStatusDialog,
            confirmStatus,
            updateValue,

            handleSelectedProcessChange,
            handleSelectedStageChange,
            handleSelectedTaskChange,
            optionMsgs,
            deleteContact,
            EnhancedEditor,
            editorRef
        
        };
    },
    mounted() {
        getDeviceInfo();

    },
};

</script>

<style scoped>
.edit-mode {
    margin-bottom: 10px;
}

.sticky-buttons {
    position: sticky;
    top: 0;
    background: white;
    z-index: 1000;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

</style>