// CloudinaryVideoTool.js

class CloudinaryVideoTool {
    static get isInline() {
      return false;
    }
  
    constructor({ data }) {
        this.data = {
          url: data?.url || '' // Use an empty string if `data.url` is undefined
        };
        this.wrapper = undefined;
      }
    
  
    render() {
      this.wrapper = document.createElement('div');
      this.wrapper.classList.add('cloudinary-video-tool');
  
      const input = document.createElement('input');
      input.placeholder = 'Enter Cloudinary video URL';
      input.value = this.data.url;
      input.addEventListener('input', (event) => {
        this.data.url = event.target.value;
      });
  
      const videoPreview = document.createElement('video');
      videoPreview.controls = true;
      videoPreview.style.display = 'none'; // Initially hide
  
      // Show video preview when a valid URL is provided
      input.addEventListener('input', () => {
        if (this.isValidCloudinaryUrl(this.data.url)) {
          videoPreview.src = this.data.url;
          videoPreview.style.display = 'block';
        } else {
          videoPreview.style.display = 'none';
        }
      });
  
      this.wrapper.appendChild(input);
      this.wrapper.appendChild(videoPreview);
      return this.wrapper;
    }
  
    save() {
      return {
        url: this.data.url,
      };
    }
  
    static get toolbox() {
      return {
        title: 'Cloudinary Video',
        icon: '<svg>...</svg>', // Add an appropriate icon here
      };
    }
  
    isValidCloudinaryUrl(url) {
      const regex = /^(https:\/\/res\.cloudinary\.com\/[\w-]+\/video\/upload\/v\d+\/.+\.mp4)$/; // Adjust regex as needed
      return regex.test(url);
    }
  }
  
  export default CloudinaryVideoTool;
  