// HelloWorldTool.js
class HelloWorldTool {
    constructor({ data }) {
        console.log("pass 2a", data);  // Check if data is received correctly
       
    
        // Use config.data directly since you're passing a hardcoded string
        this.data = data || ""; 
        this.wrapper = null;
      }
    static get toolbox() {
        return this.data;
    }

    render() {
        // Array of strings that define which columns to display
        const displayColumns = ["no", "name", "phoneNumber", "rating", "project","interaction","status"]; // Data fields to display
    
        // Container to hold the title and the table
        const container = document.createElement('div');
    
       
     
        
        let title = document.createElement('h2');
        title.innerText = this.data.title;
        title.style.textAlign = 'center';
        title.style.marginBottom = '20px'; // Add space between title and table
        // Append the title to the container
        container.appendChild(title);

    
        // Create the table element
        const table = document.createElement('table');
        table.style.width = '100%';
        table.style.borderCollapse = 'collapse';
    
        const thead = document.createElement('thead');
        const tbody = document.createElement('tbody');
    
        // Define headers for the columns
        const headerRow = document.createElement('tr');
        const headers = {
            id: 'ID',                // Example header mapping
            name: 'Name',
            phoneNumber: 'Phone Number',
            rating: 'Rating',
            project: 'Project',
            no: 'No',
            interaction:'Interaction',
            called:'Called',
            status:'Status'
        };
    
        // Create headers that are in displayColumns and match the data fields
        displayColumns.forEach(column => {
            if (headers[column]) { // Check if the header exists
                const th = document.createElement('th');
                th.innerText = headers[column];
    
                // Inverted header style
                th.style.backgroundColor = '#333'; // Dark background
                th.style.color = 'white';           // Light text color
                th.style.border = '1px solid #ddd'; // Border for header
                th.style.padding = '8px';           // Padding for header
                headerRow.appendChild(th);
            }
        });
        thead.appendChild(headerRow);
    
        // Create body rows
        let filter = this.data.filter; // The filter object
        let filterField = Object.keys(filter)[0]; // The field to filter by (e.g., "rating")
        let filterType = Object.keys(filter[filterField])[0]; // The filter type (e.g., "gt", "eq")
        let filterValue = filter[filterField][filterType]; // The value to compare against
        for (let i = 0; i < this.data.rows.length; i++) {
          //  filter: {"rating":{"gt":0}},
     
       
        
            // Apply filter conditions based on filterType
            if (filterType === "gt" && this.data.rows[i][filterField] <= filterValue) continue;
            if (filterType === "eq" && this.data.rows[i][filterField] !== filterValue) continue;
            if (filterType === "neq" && this.data.rows[i][filterField] === filterValue) continue;
            if (filterType === "lt" && this.data.rows[i][filterField] >= filterValue) continue;
            if (filterType === "ct" && !this.data.rows[i][filterField].includes(filterValue)) continue;
            
            const row = document.createElement('tr');
    
            // Loop through each displayColumn and only add those columns
            displayColumns.forEach(column => {
                const cell = document.createElement('td');
                const cellData = this.data.rows[i][column]; // Get the data for the matching column
    
                cell.innerText = cellData !== undefined ? cellData : ''; // Display cell data or empty string
    
                // Set border, padding, and alternate row colors
                cell.style.border = '1px solid #ddd';
                cell.style.padding = '8px';
    
                // Alternate row colors
                if (i % 2 === 0) {
                    cell.style.backgroundColor = '#f2f2f2'; // Light grey for even rows
                } else {
                    cell.style.backgroundColor = '#ffffff'; // White for odd rows
                }
    
                row.appendChild(cell);
            });
    
            tbody.appendChild(row);
        }
    
        table.appendChild(thead);
        table.appendChild(tbody);
    
        // Append the table to the container
        container.appendChild(table);
    
        // Return the container with both the title and the table
        return container;
    }
    

    save() {
        return this.data; // Save data if needed, or modify as per your requirements
    }
}

export default HelloWorldTool;
