<template>
    <div class="note-item-container">
        <div class="note-item-row" :style="{ paddingLeft: `${level * 20}px` }">
            <span @click="toggleChildren" style="cursor: pointer;">
                {{ isOpen ? '🔽' : '▶️' }}
            </span>
            <span @click="toggleShowDetails" style="cursor: pointer; margin-left: 5px;"
                :class="{ 'highlighted': note.id === activeNoteId }">
                {{ note.attributes.name }}
            </span>
        </div>

        <!-- Render children inside a collapsible container -->
        <div v-if="isOpen" class="note-children">
            <NoteItem v-for="child in note.attributes.child_ids?.data || []" :key="child.id" :note="child" :parentName="note.attributes.name"
                :level="level + 1" :activeNoteId="activeNoteId" :onSave="onSave" @updateActiveNote="updateActiveNote" />
        </div>

        <!-- Show EnhancedEditor only if the current note is active -->
        <div v-if="note.id === activeNoteId && loaded" class="sidebar">
            <h1>{{ parentName || '' }}</h1>
            <EnhancedEditor :key="note.attributes.block" ref="editorRef" :initialData="pdata()" />
            <v-btn @click="saveContact" color="primary" class="mx-1" rounded
                style="width: 10%; min-width: 10px;height: 36px;">Save</v-btn>
        </div>

        
    </div>
   
</template>

<script>
import { ref } from 'vue';
import EnhancedEditor from './EnhancedEditor.vue';
import { loadContacts } from '@/utils/contactUtils';
import cloneDeep from 'lodash/cloneDeep';

export default {
    components: {
        EnhancedEditor
    },
    props: {
        note: {
            type: Object,
            required: true
        },
        level: {
            type: Number,
            default: 0
        },
        activeNoteId: {
            type: String,
            required: true
        },
        parentName: {
            type: String,
            required: true
        },
        onSave: Function, // Ensure onSave is passed down
    },
    setup(props, { emit }) {
        const editorRef = ref(null);
        const isOpen = ref(false);
        const blockContent = ref({});
        const loaded = ref(false);
        const contacts = ref([]);
        const toggleChildren = () => {
            isOpen.value = !isOpen.value;
        };
        if (props.note.attributes.type == "h")
            loaded.value = false;
        else
            loaded.value = true;
        const pdata = () => {


            let dupBlock = {};
            if (props.note.attributes.type == "h")
            dupBlock = cloneDeep(props.note.attributes.block);
               else if (props.note.attributes.block)
               dupBlock = cloneDeep(props.note.attributes.block.blocks);

            if (Array.isArray(dupBlock)) {
                for (let ii = 0; ii < dupBlock.length; ii++) {
                    console.log("-->", ii);
                    dupBlock[ii].data.rows = contacts.value;
                    dupBlock[ii].data.parentName = props.parentName;
                }
            } else {
                // If it's not an array, handle it accordingly
                console.error('aa is not an array:', dupBlock);
            }
            if (props.note.attributes.type == "h") {
                return {
                    "time": 1729030092472,
                    "blocks": dupBlock,
                    "version": "2.30.6"
                }
            }
            else {
                loaded.value = true;
                return props.note.attributes.block
            }
        }

        const fetchData = async () => {
            console.log("load data...");
            contacts.value = await loadContacts(props.parentName);
            if (contacts.value) {
                loaded.value = true;
            }
        };

        const toggleShowDetails = () => {
            if (props.note.id === props.activeNoteId) {
                emit('updateActiveNote', null); // Deselect if clicked again
                loaded.value = false;
            } else {
                emit('updateActiveNote', props.note.id); // Select this note


                if (!loaded.value)
                    fetchData();
            }
        };

        const saveTaskToContact = async () => {
            try {
                const savedData = await editorRef.value.getSavedData();
                console.log('Data to save to server:', savedData);
                blockContent.value = savedData;
            } catch (error) {
                console.error('Failed to get saved data:', error);
            }
        };

        const saveContact = async () => {
            await saveTaskToContact();
            props.onSave(props.note.id, blockContent.value);
        };

        // Ensure this method exists so it can be passed down to children
        const updateActiveNote = (noteId) => {
            emit('updateActiveNote', noteId); // Emit the event to the parent
        };

        return {
            editorRef,
            isOpen,
            toggleChildren,
            toggleShowDetails,
            updateActiveNote,
            saveContact,
            fetchData,
            loaded,
            pdata
        };
    }
};
</script>

<style scoped>
.highlighted {
    background-color: yellow;
    /* Change this to your desired highlight color */
    font-weight: bold;
    /* Optional: make the text bold */
    transition: background-color 0.3s;
    /* Optional: add a smooth transition */
}




</style>