<template>
  <v-container style="background-color: aliceblue">
    <v-card class="mx-auto" max-width="1200px">
      <v-card-text>
        <div id="editorjs" style="min-height: 300px;"></div>
      </v-card-text>
    </v-card>
  </v-container>

  <v-row class="mt-4">
    <v-col cols="12">
      <v-btn @click="saveContent" color="primary">Save Content</v-btn>
    </v-col>
  </v-row>

  <v-row class="mt-4">
    <v-col cols="12">
      <v-card>
        <v-card-title>Saved Output</v-card-title>
        <v-card-text>
          <pre>{{ savedData }}</pre>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import EditorJS from '@editorjs/editorjs';
import Header from '@editorjs/header';
import List from '@editorjs/list';
import Quote from '@editorjs/quote';
import Table from '@editorjs/table';
import Checklist from '@editorjs/checklist';
import Embed from '@editorjs/embed';
import Delimiter from '@editorjs/delimiter';
import SimpleImage from '@editorjs/simple-image';
import dbTableTool from './dbTableTool'; // Import your HelloWorld tool
import editorjsNestedChecklist from '@calumk/editorjs-nested-checklist';
import Paragraph from '@editorjs/paragraph';
import CloudinaryVideoTool from './CloudinaryVideoTool';
import marker from './MarkerTool';
import HelloWorldTool from './ToolInsetOptionWords';
import SelectWords from './ToolSelectWords';


export default {
  name: "EnhanceEditor",
  props: {
    initialData: {
      type: Object, // Change from String to Object
      required: true // Ensure it's required if necessary
    }
  },
  data() {
    return {
      editor: null,
      savedData: null,
    };
  },
  mounted() {
    this.initEditor();
  },
  beforeUnmount() {
    if (this.editor) {
      this.editor.destroy();
    }
  },
  methods: {
    initEditor() {

      // const data = this.initialData || '{}';
      const data = this.initialData || '{}';
      console.log("pass ee1", data);


      this.editor = new EditorJS({
        holder: 'editorjs',
        data: data,
        tools: {
          nestedchecklist: editorjsNestedChecklist,
          paragraph: {
            class: Paragraph,
            inlineToolbar: true,
            config: {
              placeholder: 'Type here...',
            },
          },
          header: {
            class: Header,
            inlineToolbar: true,
            config: {
              placeholder: 'Enter a heading',
            },
          },
          list: {
            class: List,
            inlineToolbar: true,
          },
          cloudinaryVideo: {
            class: CloudinaryVideoTool,
          },
          image: {
            class: SimpleImage,
          },
          quote: {
            class: Quote,
            inlineToolbar: true,
            config: {
              quotePlaceholder: 'Enter a quote',
              captionPlaceholder: 'Quotes author',
            },
          },
          table: {
            class: Table,
            inlineToolbar: true,
            config: {
              rows: 2,
              cols: 3,
            },
          },
          checklist: {
            class: Checklist,
            inlineToolbar: true,
          },
          embed: {
            class: Embed,
            inlineToolbar: true,
          },
          delimiter: Delimiter,

          dbTable: {
            class: dbTableTool,
            inlineToolbar: true,
           
          },
            vueTool: marker, // Register your Vue tool here
            InsertText:HelloWorldTool,
            SelectWords:SelectWords,
          },
          toolbar: {
            inlineToolbar: true, // If you want the inline toolbar
            tools: {
              vueTool: {
                title: 'Vue Tool', // Title to display in the toolbar
                icon: '<span>VT</span>', // Icon to display in the toolbar
              },
              // Other tools can be added here as well
            },
          },

        });
    },
    saveContent() {
      this.editor.save().then((outputData) => {
        this.savedData = JSON.stringify(outputData, null, 2);
        console.log('Saved Data:', outputData);
      }).catch((error) => {
        console.error('Saving failed: ', error);
      });
    },
    async getSavedData() {
      try {
        const outputData = await this.editor.save();
        return outputData;
      } catch (error) {
        console.error('Failed to get saved data:', error);
        return null;
      }
    },
  },
};
</script>

<style scoped>
#editorjs {
  background: #f9f9f9;
  padding: 16px;
  border-radius: 4px;
}

.simple-image.withBorder img {
  border: 1px solid #e8e8eb;
}

.simple-image.withBackground {
  background: #eff2f5;
  padding: 10px;
}

.simple-image.withBackground img {
  display: block;
  max-width: 60%;
  margin: 0 auto 15px;
}

pre {
  background-color: #a4e4f4;
  border: 1px solid #ccc;
  padding: 10px;
}


</style>
