<template>
  <v-container>
    <NoteHierarchy/>
    <h2>Contact Management V1.1</h2>

    <!-- <v-row class="sticky-buttons">
      <v-col cols="12">
        <v-select v-model="filterTerm" :items="filterOptions" label="Filter Contacts"></v-select>
      </v-col>
    </v-row> -->

  

    <v-row>
      <v-col cols="12" md="6">
        <input class="bordered-input" style="color: white; background-color: #333; height: 50px;" v-model="filterTerm"
          placeholder="Filter Search... r/dup/any text">
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="loadByProject(fetchContactsRatingQuery)" color="primary">Load By Rating</v-btn>
      </v-col>

      <v-col cols="12" md="6">
        <v-text-field v-model="projectFilter" label="Enter project name" outlined />
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="loadByProject(fetchContactsQuery)" color="primary">Load By Project</v-btn>
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="loadByProject(fetchContactsFilterQuery)" color="primary">Load By Project with Filter</v-btn>
      </v-col>

    </v-row>
    <!-- Select a user -->
    <v-select v-model="selectedUser" :items="users" item-text="text" item-value="value" label="Select a user"
      style="width: 50%; min-width: 120px;"></v-select>

    <!-- Select a phone ID -->
    <v-select v-model="selectedPhoneId" :items="phoneIds" item-text="text" item-value="value" label="Select a Phone ID"
      style="width: 50%; min-width: 120px;"></v-select>


    <!-- Contact List Table -->
    <!-- Filter and Load Data -->


    <!-- Sticky Buttons Row -->
    <v-row class="sticky-buttons">
      <v-col cols="2" md="1">
        <v-btn @click="openAddForm" color="success" class="mb-4" icon>
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="editSelectedContact" :disabled="!selectedIndex === 0" color="primary" class="mb-4" icon>
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="prevContact" :disabled="selectedIndex === 0" color="primary" class="mr-2" icon>
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-col>
      <v-col cols="2" md="1">
        <v-btn @click="nextContact" :disabled="selectedIndex === contacts.length - 1" color="primary" icon>
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </v-col>
    </v-row>


    <v-tabs v-if="!loading && !error && contacts.length && !currentContact && !isAdding" v-model="selectedTab"
      background-color="white" slider-color="blue">
      <v-tab>Basic Info</v-tab>
      <v-tab>Contact Details</v-tab>
      <v-tab>Additional Info</v-tab>
      <v-tab>Time & Date</v-tab>

    </v-tabs>


    <!-- Contacts Table -->
    <v-data-table
      v-if="filteredContacts.length && !loading && !error && contacts.length && !currentContact && !isAdding"
      :headers="filteredHeaders" :items="filteredContacts" item-key="id" class="elevation-1" style="width: 100%">
      <!-- Contact List Table -->
      <!-- <v-data-table :headers="filteredHeaders" :items="filteredContacts" item-key="id" class="elevation-1"
      style="width: 100%"> -->


      <template v-slot:item="{ item, index }">
        <tr :class="getRowClass(index)" @click="focusRow(item, index)">
          <!-- Display Columns based on Tab -->
          <td v-if="showColumn('no')" width="5">{{ item.no }}</td>
          <td v-if="showColumn('project')" width="50">{{ item.project }}</td>
          <td v-if="showColumn('name')" width="70">{{ item.name }}</td>
          <td v-if="showColumn('called')" width="10px">{{ item.called }}</td>

          <td v-if="showColumn('interaction')" width="400">{{ item.interaction }}</td>
          <td v-if="showColumn('phoneNumber')" width="70">{{ item.phoneNumber }}</td>

          <td v-if="showColumn('clientType')" width="20">{{ item.clientType }}</td>
          <td v-if="showColumn('language')" width="5">{{ item.language }}</td>

          <td v-if="showColumn('status')">{{ item.status }}</td>
          <td v-if="showColumn('rating')">{{ item.rating }}</td>
          <td v-if="showColumn('process')">{{ item.process }}</td>
          <td v-if="showColumn('stage')">{{ item.stage }}</td>
       
          <td v-if="showColumn('delegate')">{{ item.delegate }}</td>
          <td v-if="showColumn('phoneId')">{{ item.phoneId }}</td>
          <td v-if="showColumn('user')">{{ item.user }}</td>
          <td v-if="showColumn('doc')">{{ item.doc }}</td>







          <td v-if="showColumn('contacted')">{{ item.contacted }}</td>
          <td v-if="showColumn('source')">{{ item.source }}</td>
          <td v-if="showColumn('dialedDate')">{{ item.dialedDate }}</td>
          <td v-if="showColumn('sourceDate')">{{ item.sourceDate }}</td>
          <td v-if="showColumn('lastCall')">{{ item.lastCall }}</td>
          <td v-if="showColumn('nextCall')">{{ item.nextCall }}</td>




          <!-- Add more columns as needed -->
        </tr>
      </template>
    </v-data-table>
    <div v-else>No data available for this tab.</div>

    <!-- Add New Contact Button -->
    <v-row>
      <v-col>
        <v-btn @click="openAddForm">Add New Contact</v-btn>
      </v-col>
    </v-row>



    <!-- Edit Contact Form Component -->
    <EditContactForm v-if="currentContact && !isAdding" :on-delete="confirmDelete" :contact="currentContact"
      :on-save="saveContact" :nextContact="nextContact" :on-cancel="cancelEdit" :selectedUser="selectedUser"
      :selectedPhoneId="selectedPhoneId" :index="selectedIndex" :contacts="contacts" />

    <!-- Add New Contact Form -->
    <v-dialog v-model="isAdding" persistent max-width="600px">
      <v-card>
        <v-card-title>Add New Contact</v-card-title>
        <v-card-text>
          <v-text-field v-model="newContactData.name" label="Contact Name"></v-text-field>
          <v-text-field v-model="newContactData.phoneNumber" label="Phone Number"></v-text-field>
          <p> Project: {{ newContactData.project }} </p>
          <p> No: {{ newContactData.no }} </p>
        </v-card-text>
        <v-card-actions>
          <v-btn @click="saveNewContact">Save</v-btn>
          <v-btn @click="cancelAdd">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete Confirmation Dialog -->
    <v-dialog v-model="isDeleteDialogOpen" max-width="400px">
      <v-card>
        <v-card-title class="headline">Confirm Delete</v-card-title>
        Are you sure you want to delete this contact #{{ currentContact ? currentContact.id : "" }}?

        <v-card-text>Name: {{ currentContact ? currentContact.name : "" }}?</v-card-text>
        <v-card-text>Phone Number: {{ currentContact ? currentContact.phoneNumber : "" }}?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red" text @click="removeContact">Delete</v-btn>
          <v-btn color="grey" text @click="closeDeleteDialog">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Navigation for contacts -->

  </v-container>
</template>

<script>
import EditContactForm from "@/components/EditContactForm.vue";
import { ref, computed } from "vue";
import { deleteContact, loadByProjectFunc, updateContact, addContactRec } from '../utils/contactUtils';
import { fetchContactsRatingQuery, fetchContactsQuery, fetchContactsFilterQuery } from '../utils/message.js';
import NoteHierarchy from '../components/NoteHierarchy.vue';


export default {
  components: {
    EditContactForm,NoteHierarchy
  },
  setup() {

    const tabColumns = [
      ['no', 'project', 'name', 'called', 'interaction', 'phoneNumber', 'clientType', 'language'], // Tab 1
      ['status', 'rating', 'process', 'stage', 'task', 'delegate', 'phoneId', 'user', 'doc'], // Tab 3
      ['delegate', 'phoneId', 'user', 'contacted', 'source'], ['dialedDate', 'sourceDate', 'lastCall', 'nextCall'],
      // Tab 4 (use correct field names)
    ];

    const showColumn = (column) => {
      return tabColumns[selectedTab.value]?.includes(column);
    };

    const filteredHeaders = computed(() => {
      return headers.filter(header => showColumn(header.value));
    });
    const contacts = ref([]);
    const selectedContact = ref(null);
    const selectedIndex = ref(-1);
    const newContactData = ref({});
    const filterTerm = ref("");
    const currentContact = ref(null);
    const isAdding = ref(false);
    const loading = ref(false);
    const error = ref(null);

    const projectFilter = ref('');
    const selectedUser = ref(null);
    const selectedPhoneId = ref(null);
    const queryStr_ = ref({});
    const selectedTab = ref(0);

    const filterOptions = ["", "dup", "r", "*"]; // Add more options if necessary

    const headers = [
      { text: 'No', title: 'No', value: 'no' },
      { text: 'Project', title: 'Project', value: 'project' },
      { text: 'Name', title: 'Name', value: 'name' },
      { text: 'Called', title: 'Called', value: 'called' },
      { text: 'Interaction', title: 'Interaction', value: 'interaction' },
      { text: 'Phone Number', title: 'Phone Number', value: 'phoneNumber' },
      { text: 'Client Type', title: 'Client Type', value: 'clientType' },
      { text: 'Language', title: 'Language', value: 'language' },
      { text: 'Status', title: 'Status', value: 'status' },
      { text: 'Rating', title: 'Rating', value: 'rating' },
      { text: 'Process', title: 'Process', value: 'process' },
      { text: 'Stage', title: 'Stage', value: 'stage' },
  
      { text: 'Delegate', title: 'Delegate', value: 'delegate' },
      { text: 'Phone ID', title: 'Phone ID', value: 'phoneId' },
      { text: 'User', title: 'User', value: 'user' },
      { text: 'Doc', title: 'Doc', value: 'doc' },
      { text: 'Contacted', title: 'Contacted', value: 'contacted' },
      { text: 'Source', title: 'Source', value: 'source' },
      { text: 'Dialed Date', title: 'Dialed Date', value: 'dialedDate' },
      { text: 'Source Date', title: 'Source Date', value: 'sourceDate' },
      { text: 'Last Call', title: 'Last Call', value: 'lastCall' },
      { text: 'Next Call', title: 'Next Call', value: 'nextCall' }
    ];

    const isDeleteDialogOpen = ref(false);
    const contactToDelete = ref(null);

    const confirmDelete = () => {
    
     // currentContact.value = { ...rec.value };
      isDeleteDialogOpen.value = true;
   //   currentContact.value = null;
    };

    async function removeContact  ()  {
      if (currentContact.value.id !== null) {
       await deleteContact(currentContact.value.id);
        contacts.value = [];
        loadByProject(queryStr_.value);
        isDeleteDialogOpen.value = false;
        currentContact.value = null;
        // contacts.value.splice(contactToDelete.value, 1);
        // contactToDelete.value = null;
        // isDeleteDialogOpen.value = false;
      }
    }

    async function saveContact(updatedData) {
      if (currentContact.value.id) {
        await updateContact(currentContact.value.id, updatedData);
      }
      contacts.value = [];
      loadByProject(queryStr_.value);
      // alert("data save!");
      currentContact.value = null;
    }

    const closeDeleteDialog = () => {
      isDeleteDialogOpen.value = false;
      contactToDelete.value = null;
    };

    const getDuplicateRecords = () => {
      // Add logic to find duplicates based on specific fields (e.g., phone number)
      const seen = new Set();
      return contacts.value.filter((contact) => {
        const isDuplicate = seen.has(contact.phoneNumber);
        seen.add(contact.phoneNumber);
        return isDuplicate;
      });
    };

    const filteredContacts = computed(() => {
      if (filterTerm.value === "") {
        return contacts.value; // Show all records
      } else if (filterTerm.value.toLowerCase() === "dup") {
        return getDuplicateRecords(); // Show only duplicate records
      } else if (filterTerm.value.toLowerCase() === "r") {
        return contacts.value.filter((contact) => contact.rating > 0); // Show records with rating > 0
      } else {
        return contacts.value.filter((contact) => {
          // Define the specific fields you want to search through
          const fieldsToSearch = [
            contact.name,
            contact.phoneNumber,
            contact.status,
            contact.interaction,
          ];
          return fieldsToSearch.some((value) =>
            value.toString().toLowerCase().includes(filterTerm.value.toLowerCase())
          );
        });
      }
    });

    function getRowClass(index) {
      return index === selectedIndex.value ? 'highlighted-row' : (index % 2 === 0 ? 'even-row' : 'odd-row');
    }

    function editSelectedContact() {
      if (selectedContact.value) {
        currentContact.value = { ...selectedContact.value };
      }
    }


    async function loadByProject(queryStr) {
      queryStr_.value = queryStr;
      loading.value = true;
      error.value = null;
      try {
        contacts.value = await loadByProjectFunc(
          projectFilter.value,
          contacts.value,
          new Date().toLocaleDateString('en-GB', { day: '2-digit', month: '2-digit', year: 'numeric' }),
          queryStr
        );
        contacts.value.sort((a, b) => {
          if (a["no"] < b["no"]) return -1;
          if (a["no"] > b["no"]) return 1;
          return 0;
        });

      } catch (err) {
        console.error('Error loading contacts:', err);
        error.value = 'An error 211 occurred while loading contacts. Please try again.';
      } finally {
        loading.value = false;
      }
    }



    const focusRow = (item, index) => {
      selectedContact.value = item;
      selectedIndex.value = index;
      //     currentContact.value = contacts.value[index];
    };

    // const saveContact = async (updatedContact) => {
    //   try {
    //     await updateContact(updatedContact);
    //     contacts.value[selectedIndex.value] = updatedContact;
    //     currentContact.value = null;
    //   } catch (e) {
    //     error.value = e.message;
    //   }
    // };



    const cancelEdit = () => {
      currentContact.value = null;
    };

    const openAddForm = () => {
      if (projectFilter.value.trim() === "") {
        alert("Please enter project name");
        return;
      }
      isAdding.value = true;
      if (contacts.value.length == 0)
        newContactData.value.no = 1;
      else
        newContactData.value.no = Math.max(...contacts.value.map(contact => contact.no || 0)) + 1;
      newContactData.value.project = projectFilter.value;
    };

    const saveNewContact = async () => {
      try {
        await addContactRec(newContactData.value);
        contacts.value = [];
        //contacts.value.push(newContactData.value);
        if (Object.keys(queryStr_.value).length === 0) {
          projectFilter.value = newContactData.value.project;
          loadByProject(fetchContactsQuery);
        }
        else {
          loadByProject(queryStr_.value);
        }
        newContactData.value = {};
        isAdding.value = false;
      } catch (e) {
        error.value = e.message;
      }
    };

    const cancelAdd = () => {
      isAdding.value = false;
      newContactData.value = {};
    };

    const prevContact = () => {
      if (selectedIndex.value > 0) {
        selectedIndex.value -= 1;
        currentContact.value = contacts.value[selectedIndex.value];
      }
    };

    const nextContact = () => {
      if (selectedIndex.value < contacts.value.length - 1) {
        selectedIndex.value += 1;
        currentContact.value = contacts.value[selectedIndex.value];
      }
    };

    // onMounted(() => {
    //   loadContactsFromStrapi2();
    // });

    return {
      getRowClass,
      users: ["Alyssa", "Ashton", "Alvin"],
      phoneIds: ["Alyssa H", "Ashton H", "Alvin H"],
      editSelectedContact,



      headers,
      contacts,
      filterTerm,
      filterOptions,
      filteredContacts,
      loading,
      error,
      currentContact,
      isAdding,
      newContactData,
      projectFilter,
      selectedUser,
      selectedPhoneId,
      selectedIndex,
      getDuplicateRecords,
      loadByProject,
      focusRow,
      saveContact,
      cancelEdit,
      openAddForm,
      saveNewContact,
      cancelAdd,
      prevContact,
      nextContact,
      filteredHeaders,
      showColumn,
      selectedTab,
      fetchContactsQuery,
      fetchContactsFilterQuery,
      fetchContactsRatingQuery,
      queryStr_,
      isDeleteDialogOpen,
      contactToDelete,
      confirmDelete,
      removeContact,
      closeDeleteDialog,
 
    };
  },
};
</script>

<style scoped>
.sticky-buttons {
  position: sticky;
  top: 0;
  background: white;
  z-index: 1000;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.highlighted-row {
  background-color: #ffe575;
}

.even-row {
  background-color: #cee7fe;
}

.odd-row {
  background-color: white;
}

.tabs-with-border {
  border-bottom: 2px solid #2196F3;
  /* Blue border line */
}
</style>
