class HelloWorldTool {
    constructor({ data }) {
        this.data = data || { selectedText: '' }; // Initialize the selected text
        this.options = ['Hello World', 'Goodbye World', 'Hello Universe', 'Goodbye Universe']; // Array of selectable options
    }

    static get toolbox() {
        return {
            title: 'Hello World',
            icon: '<span>HW</span>', // You can replace this with an SVG icon
        };
    }

    render() {
        const wrapper = document.createElement('div');

        // Create a select dropdown where users can select predefined text
        const select = document.createElement('select');
        select.innerHTML = this.options.map(option => `<option value="${option}">${option}</option>`).join(''); // Populate options

        // Set the selected value from saved data
        if (this.data.selectedText) {
            select.value = this.data.selectedText;
        }

        // Add an event listener to capture the selected value
        select.addEventListener('change', (event) => {
            this.data.selectedText = event.target.value; // Update the data on selection change
        });

        // Append the select dropdown to the wrapper
        wrapper.appendChild(select);

        return wrapper;
    }

    save() {
        return this.data; // Return the saved selected text data
    }
}

export default HelloWorldTool;
