export async function loadProjects(projectFilter) {
  const fetchProjectsQuery = gql`
      query($project: String!) {
      projects(filters: { name: { eq: $project } }, pagination: { limit: 1000 }) {
        data {
          id
          attributes {
            name
            description
            category
            reference
            developer
            completionDate
            TitleType
            unit_types {
              data {
                attributes {
                  name
                  description
                  price
                  room
                  bathRoom
                  carPark
                  buildUpArea
                  landSize      
                  unitLeft
                  furnishing
                  orientation
                }
              }
            }
            landTitle
            tenure
            yearBuilt
            address
            landDimension
            propertyType
            projectUrl
            DocUrl
            lotStatus
          }
        }
      }
    }
  `;

  try {
    const { data } = await client.query({
      query: fetchProjectsQuery,
      variables: { project: projectFilter || '' },
      fetchPolicy: 'no-cache', // Adjust fetch policy as needed
    });

    return data.projects.data.map((project) => ({
      id: project.id,
      name: project.attributes.name,
      description: project.attributes.description,
      category: project.attributes.category,
      reference: project.attributes.reference,
      developer: project.attributes.developer,
      completionDate: project.attributes.completionDate,
      TitleType: project.attributes.TitleType,
      landTitle: project.attributes.landTitle,
      tenure: project.attributes.tenure,
      yearBuilt: project.attributes.yearBuilt,
      address: project.attributes.address,
      landDimension: project.attributes.landDimension,
      propertyType: project.attributes.propertyType,
      projectUrl: project.attributes.projectUrl,
      DocUrl: project.attributes.DocUrl,
      lotStatus: project.attributes.lotStatus,
      unitTypes: project.attributes.unit_types.data.map((unit) => ({
        name: unit.attributes.name,
        description: unit.attributes.description,
        price: unit.attributes.price,
        room: unit.attributes.room,
        bathRoom: unit.attributes.bathRoom,
        carPark: unit.attributes.carPark,
        buildUpArea: unit.attributes.buildUpArea,
        landSize: unit.attributes.landSize, // Fixed typo
        unitLeft: unit.attributes.unitLeft,
        furnishing: unit.attributes.furnishing,
        orientation: unit.attributes.orientation,
      })),
    }));
  } catch (error) {
    console.error('Error loading projects:', error);
    throw error;
  }
}

export async function loadContacts(projectFilter) {

  const fetchContactsQuery = gql`
    query($project: String!) {
    leads(filters: { project: { eq: $project } }, pagination: { limit: 1000 }) {
      data {
        id
        attributes {
          no
          project
          user
          name
          phoneNumber
          status
          phoneId
          interaction
          lastCall
          nextCall
          called
          rating
          dialedDate
          sourceDate
          contacted
          language
          message
          doc
          stage
          source
          occupation
          updatedAt
          advUrl
          advUrlDate
          facebookUrl
          notionUrl
          clientType
          process
          stage
          task
          delegate
          address
        }
      }
    }
  }
`;

  try {
    const { data } = await client.query({
      query: fetchContactsQuery,
      variables: { project: projectFilter || '' },
      fetchPolicy: 'no-cache',
    });

    return data.leads.data.map((contact) => ({
      no: contact.attributes.no,
      id: contact.id,
      name: contact.attributes.name,
      phoneNumber: contact.attributes.phoneNumber,
      interaction: contact.attributes.interaction,
      status: contact.attributes.status,
      phoneId: contact.attributes.phoneId,
      doc: contact.attributes.doc,
      rating: contact.attributes.rating||0,
      called: contact.attributes.called||0,
      contacted: contact.attributes.contacted,
      advUrl: contact.attributes.advUrl,
      advUrlDate: contact.attributes.advUrlDate,
      facebookUrl: contact.attributes.facebookUrl,
      notionUrl: contact.attributes.notionUrl,

      clientType: contact.attributes.clientType,
      process: contact.attributes.process,
      stage: contact.attributes.stage,
      task: contact.attributes.task,
      delegate: contact.attributes.delegate,
      address: contact.attributes.address,
    }));
  } catch (error) {
    console.error('Error loading contacts:', error);
    throw error;
  }
}


export function isPhoneDisabled2(contact) {
  if (!isPhoneDisabled(contact)) {
    if (deviceInfo.value) {
      if (deviceInfo.value.model.toLowerCase().includes("window"))
        return 2;
      else
        return 1;
    }
    else
      return 0;
  }
  else
    return 0;
}

export function isPhoneDisabled(contact) {
  const status = String(contact.status || '');
  const interaction = String(contact.interaction || '');
  const stage = String(contact.stage || '');

  // Perform case-insensitive check and substring check
  return status.toLowerCase().includes('ni') || interaction.includes('xxx') || stage.trim().toLowerCase() == "x";

}

export function convertDMY2YMDtime(dateString) {
  if (!dateString) return null;
  try {
    // Check if the date string already contains "T" or "Z"
    if (dateString.includes('T') || dateString.includes('Z')) {
      return dateString; // Return as is if already in ISO format
    }

    // Replace all slashes with dashes and trim spaces
    const normalizedDateString = dateString.replaceAll('/', '-').trim();

    // Split the date into parts
    const dateParts = normalizedDateString.split('-');

    // Validate the parts length
    if (dateParts.length !== 3) {
      throw new Error('Invalid date format; expected 3 parts');
    }

    let [day, month, year] = dateParts.map(part => part.trim());

    // Ensure we have valid day, month, and year
    if (!day || !month || !year || isNaN(parseInt(day)) || isNaN(parseInt(month)) || isNaN(parseInt(year))) {
      throw new Error('Invalid date format');
    }

    day = parseInt(day);
    month = parseInt(month);
    year = parseInt(year);

    // Check if the first part is a day (less than 1000) or a year (greater than or equal to 1000)
    if (year < 1000) {
      // It's in DMY format, so swap day and year
      [day, year] = [year, day];
    }

    // Validate the day and month ranges
    if (month < 1 || month > 12) {
      throw new Error('Month must be between 1 and 12');
    }

    if (day < 1 || day > 31) {
      throw new Error('Day must be between 1 and 31');
    }

    // Pad month and day with leading zeros if needed
    const formattedDay = String(day).padStart(2, '0');
    const formattedMonth = String(month).padStart(2, '0');

    // Create the ISO string
    const isoDateString = `${year}-${formattedMonth}-${formattedDay}`;

    return `${isoDateString}T00:00:00Z`; // Append time only if not already present

  } catch (error) {
    console.error('Error in convertDMY2YMDtime:', error.message);
    return null;  // Return null if the date format is invalid
  }
}


export function convertDMY2YMD(dateString) {
  if (!dateString) return null;
  let [day, month, year] = dateString.replaceAll('/', '-').split('-');
  if (day < 1000) {  // convert only if day is not a year 
    month = month.padStart(2, '0');
    day = day.padStart(2, '0').replace("Z", ""); //sometime Z came out, need to remove
    return `${year}-${month}-${day}`;
  }
  else {
    return `${day}-${month}-${year}`;
  }
}
export function sortContactsAndNumber(property, contacts, sortOrder) {
  if (sortOrder[property] === 'asc') {
    contacts.sort((a, b) => {
      if (a[property] + formatNumberWithLeadingZeros(a['no']) < b[property] + formatNumberWithLeadingZeros(b['no'])) return -1;
      if (a[property] + formatNumberWithLeadingZeros(a['no']) > b[property] + formatNumberWithLeadingZeros(b['no'])) return 1;
      return 0;
    });
    sortOrder[property] = 'desc';
  } else {
    contacts.sort((a, b) => {
      if (a[property] + a['no'] < b[property] + a['no']) return 1;
      if (a[property] + a['no'] > b[property] + a['no']) return -1;
      return 0;
    });
    sortOrder[property] = 'asc';
  }
}

export function formatNumberWithLeadingZeros(number) {
  // Convert the number to a string
  const numStr = String(number);
  // Pad the string with leading zeros
  const paddedStr = numStr.padStart(6, '0');
  return paddedStr;
}

export function extractCountryCode(phoneNumber) {
  const phone = parsePhoneNumberFromString(phoneNumber);
  return phone ? phone.countryCallingCode : null;
}

export function formatDateToYMD(date) {
  const yearStr = date.getFullYear();
  const monthStr = String(date.getMonth() + 1).padStart(2, '0');
  const dayStr = String(date.getDate()).padStart(2, '0');
  return `${yearStr}-${monthStr}-${dayStr}`;
}
export function removeCountryCode(phone, ccode) {
  // Check if the phone number starts with '+60'
  if (phone.startsWith('+' + ccode)) {
    return phone.slice(1 + ccode.length); // Remove the first 3 characters ('+60')
  }
  // Check if the phone number starts with '60'

  if (phone.startsWith(ccode)) {
    return phone.slice(ccode.length); // Remove the first 2 characters ('60')
  }
  // Check if the phone number starts with '(+60)'
  if (phone.startsWith('(+' + ccode + ')')) {
    return phone.slice(3 + ccode.length); // Remove the first 5 characters ('(+60)')
  }

  return phone; // Return the phone as is if no matching prefix is found
}
// Fetch contacts by project filter
export function formatPhoneNumber2(phone) {
  if (phone) {
    let ccode = extractCountryCode(phone);
    if (ccode === null) {
      phone = "+60" + phone;
      return phone.trim();
    }
    else {
      phone = removeCountryCode(phone, ccode);
      phone = phone.replaceAll(' ', '');
      phone = phone.replaceAll('-', '');
    }
    // Check if the phone number doesn't start with '+60', '60', or '(+60)'


    return '+' + ccode + phone.trim();
  }
  else
    return '';
}

export async function loadByProjectFunc(projectFilter, contacts, importDate, gql) {


  try {
    const { data } = await client.query({
      query: gql,
      variables: { project: projectFilter },
      fetchPolicy: 'no-cache',
    });

    data.leads.data.forEach((lead) => {
      const existingContact = contacts.find(contact => contact.phoneNumber === lead.attributes.phoneNumber);

      const newContact = {
        id: lead.id,
        no: lead.attributes.no,
        user: lead.attributes.user || '',
        name: lead.attributes.name || '',
        phoneNumber: formatPhoneNumber2(lead.attributes.phoneNumber) || '',
        project: lead.attributes.project || '',
        occupation: lead.attributes.occupation || '',
        status: lead.attributes.status || '',
        phoneId: lead.attributes.phoneId || '',
        interaction: lead.attributes.interaction || '',
        lastCall: convertYMD2DMY(lead.attributes.lastCall) || '1-1-1900',
        nextCall: convertYMD2DMY(lead.attributes.nextCall) || '1-1-1900',
        called: lead.attributes.called || 0,
        rating: lead.attributes.rating || 0,
        dialedDate: convertYMD2DMY(lead.attributes.dialedDate) || '1-1-1900',
        sourceDate: convertYMD2DMY(lead.attributes.sourceDate) || '1-1-1900',
        contacted: lead.attributes.contacted || 0,
        language: lead.attributes.language || '',
        message: lead.attributes.message || '',
        doc: lead.attributes.doc || '',
        source: lead.attributes.source || '',
        clientType: lead.attributes.clientType || '',
        process: lead.attributes.process || '',
        stage: lead.attributes.stage || '',
        task: lead.attributes.task || '',
        delegate: lead.attributes.delegate || '',
        address: lead.attributes.address || '',
        advUrl: lead.attributes.advUrl || '',
        advUrlDate: lead.attributes.advUrlDate || '',
        facebookUrl: lead.attributes.facebookUrl || '',
        notionUrl: lead.attributes.notionUrl || '',
        edit: false,
        editField: '',
        updatedAt: importDate,




      };

      if (existingContact) {
        Object.assign(existingContact, newContact);
      } else {
        contacts.push(newContact);
      }
    });
  } catch (error) {

    alert("An error 212 occurred while loading contacts. Please try again.");
  }

  return contacts; // Returning the updated contacts array
}


export function convertYMD2DMY(dateString) {
  // Check if dateString is valid
  if (!dateString) return null;

  // Parse the date string to a Date object
  const date = new Date(dateString);

  // Check if the date is the default invalid date (1900-01-01)
  if (date.getFullYear() === 1900 && date.getMonth() === 0 && date.getDate() === 1) {
    return null;
  }

  // Format the date as DD/MM/YYYY
  const dayStr = String(date.getDate()).padStart(2, '0');
  const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const yearStr = date.getFullYear();

  return `${dayStr}/${monthStr}/${yearStr}`;
}
