<template>
    <div>
      <viewProject/>
        <h1>Note Hierarchy</h1>

        <div v-if="loading">Loading notes...</div>
        <div v-if="error">{{ error.message }}</div>

        <table>
            <tbody>
                <!-- Render only top-level notes -->
                <NoteItem v-for="note in topLevelNotes" :key="note.id" :note="note" :level="0"
                    :activeNoteId="activeNoteId" :on-save="saveContact" @updateActiveNote="setActiveNote" />


            </tbody>
        </table>
    </div>
</template>

<script>
import { gql } from '@apollo/client';
import { ref, computed } from 'vue';
import client from '../apollo-client';
import NoteItem from './NoteItem.vue';
import viewProject from './viewProject.vue';

const GET_ALL_NOTE_TAKES = gql`
  query GetAllNoteTakes {
  noteTakes(
    sort: "name", 
    filters: { parentIds: { id: { eq: null } } }, 
    pagination: { limit: 1000 }
  ) {
    data {
      id
      attributes {
        name
        type
        description
        hide
        emoji
        block
        parentIds {
          data {
            id
            attributes {
              name
            }
          }
        }
        child_ids {
          data {
            id
            attributes {
              name
              type
              description
              hide
              emoji
              block
              parentIds {
                data {
                  id
                  attributes {
                    name
                  }
                }
              }
              child_ids {
                data {
                  id
                  attributes {
                    name
                    type
                    description
                    hide
                    emoji
                    block
                    parentIds {
                      data {
                        id
                        attributes {
                          name
                        }
                      }
                    }
                    child_ids {
                      data {
                        id
                        attributes {
                          name
                          type
                          description
                          hide
                          emoji
                          block
                          
                          
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

  `;

export default {
    components: {
        NoteItem,
        viewProject
    },
    setup() {
        const notes = ref([]);
        const loading = ref(true);
        const error = ref(null);

        const activeNoteId = ref(null); // Stores the active note ID

        const setActiveNote = (noteId) => {
            activeNoteId.value = noteId;
        };

        const UPDATE_NOTE_MUTATION = gql`
  mutation UpdateNoteBlock($id: ID!, $block: JSON!) {
    updateNoteTake(id: $id, data: { block: $block }) {
      data {
        id
        attributes {
          block
        }
      }
    }
  }
`;

        async function saveContact(noteId, updatedBlockContent) {
            try {
                console.log("--1");

                const findNoteIndex = (notesArray, id) => {
                    for (let i = 0; i < notesArray.length; i++) {
                        if (notesArray[i].id === id) {
                            return i; // Return index if found
                        }
                        // Check for children if they exist
                        if (notesArray[i].attributes.child_ids?.data) {
                            const childIndex = findNoteIndex(notesArray[i].attributes.child_ids.data, id);
                            if (childIndex !== -1) {
                                return childIndex; // Return child index if found
                            }
                        }
                    }
                    return -1; // Return -1 if not found
                };
                const noteIndex = findNoteIndex(notes.value, noteId);
                // Find the note to be updated by ID
                //     const noteIndex = notes.value.findIndex(note => note.id === noteId);
                if (noteIndex === -1) return;


                // Deep copy the blocks array using JSON.parse and JSON.stringify
                const updatedBlocks = updatedBlockContent;

                // Call the update mutation to save changes to the server
                await client.mutate({
                    mutation: UPDATE_NOTE_MUTATION,
                    variables: {
                        id: noteId,
                        block: updatedBlocks, // Pass the new blocks array
                    },
                });

                console.log('Note updated successfully!--4');
            } catch (err) {
                console.error('Error updating note:--5', err);
            }
        }


        // Fetch notes data using Apollo Client
        const fetchNotes = async () => {
            try {
                const response = await client.query({
                    query: GET_ALL_NOTE_TAKES,
                });
                notes.value = response.data.noteTakes.data;
            } catch (err) {
                error.value = err;
                console.error('Error loading notes:', err);
            } finally {
                loading.value = false;

            }
        };






        // Fetch notes when the component is mounted
        fetchNotes();

        // Filter for top-level notes (without a parent)
        // const topLevelNotes = computed(() => {
        //     return notes.value.filter(note => !note.attributes.parentIds.data);
        // });

        const topLevelNotes = computed(() => {
            return notes.value.filter(note => !note.attributes.parentIds.data || note.attributes.parentIds.data.length === 0);
        });
        return {
            notes,
            loading,
            error,
            topLevelNotes,
            activeNoteId,
            setActiveNote,
            saveContact
        };
    },
};
</script>

<style scoped>
table {
    width: 100%;
    border-collapse: collapse;
}

th,
td {
    border: 1px solid #ddd;
    padding: 6px;
}

th {
    background-color: #f2f2f2;
}


</style>