import { marked } from 'marked'; // Import the marked library for Markdown parsing
// HelloWorldTool.js
class HelloWorldTool {
    constructor({ data }) {
        this.data = data || {};
        this.textOptions = [`# Hello World\nThis is a **test**.`,`
            > #### The quarterly results look great!
>
> - Revenue was off the chart.
> - Profits were higher than ever.
>
>  *Everything* is going according to **plan**.
# Property Information
------------------------------
Property Name: ____

**Quick Facts**:

**Project Name**: ____


    * Location: Opposite ____, 5 minutes to ____
    * Land Size: 2222 sq. ft.
    * Total Units:
    * Block A:
        * Stories: 7
        * Units: ##
        * Price: ____
    * Block B:
        * Stories: 7
        *  Units: ##
        * Price: ____
 **Target Completion Date: YYYY, MM (## months from SPA)**

 **Developer:** ____  
________________________
 # Main Unit Layouts:  
    * Type A:  
        * Studio  
        * Size: 504 sq. ft.  
    * Type B:
        * 1 + 1 Room
        * Bathroom: 1
        * Car Park: Yes/No (please specify)
    * Type C:
        * Description: ____
        * Size: ____
        * Title Information:

* Leasehold or Freehold: ____
* Highlights:

(Please list the key features or selling points of the property.)

            `, 'Sample Text', 'Another Option']; // Example text options
    }

    static get toolbox() {
        return {
            title: 'Hello World',
            icon: '<span>HWA</span>', // You can replace this with an SVG icon
        };
    }

    render() {
        const wrapper = document.createElement('div');
      //  wrapper.innerHTML = '<h1>Hello World</h1>'; // Initial text
        this.showPopup(wrapper); // Show popup immediately
        return wrapper;
    }

    showPopup(wrapper) {
        const popup = document.createElement('div');
        popup.style.position = 'absolute'; // Position the popup
      //  popup.style.backgroundColor = '#fff';
      //  popup.style.border = '1px solid #ccc';
        popup.style.zIndex = '1000'; // Make sure the popup is above other content
      //  popup.style.padding = '10px';
        popup.style.boxShadow = '0 4px 8px rgba(0, 0, 0, 0.1)';
        
        this.textOptions.forEach((text) => {
            const option = document.createElement('div');
          //  option.innerText = text;
            option.innerHTML = marked(text); // Convert Markdown to HTML and replace newline with <br>
          
            option.style.cursor = 'pointer';
            option.style.padding = '5px';
            option.style.margin = '2px 0';
            option.addEventListener('click', () => {
                this.insertText(wrapper, text); // Pass wrapper to insertText
                wrapper.removeChild(popup); // Remove the popup after selection
            });

            popup.appendChild(option);
        });

        wrapper.appendChild(popup);
    }

    insertText(wrapper, selectedText) {
        const textElement = document.createElement('div');
        textElement.contentEditable = true; // Allow the text to be editable
        textElement.innerHTML = marked(selectedText); // Insert the selected text
        textElement.style.border = '1px solid #ddd'; // Optional styling for visibility
        textElement.style.padding = '5px';
        textElement.style.marginTop = '5px';
        textElement.style.minHeight = '20px'; // Set a minimum height for better usability

        // Append the editable text element to the wrapper
        wrapper.appendChild(textElement);
        
        // Set focus on the newly inserted text element for immediate editing
        textElement.focus();
    }

    save() {
        return this.data; // Save data if needed, or modify as per your requirements
    }
}

export default HelloWorldTool;
